.container-login {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 450px;
    justify-content: center;
    margin: auto;
    padding: 50px;
    box-shadow: rgba(0, 0, 0, .35)0px 5px 15px;
    border-radius: 20px;
    align-items: baseline;
    margin-top: 50px;
  
}

.container-login h1{
    margin: 0 auto;
    text-align: left;
    margin-bottom: 10px;
}

.container-login .login-form {
    padding: 5px;
   
}

.container-login .login-form .login-form-group .form-field {
    display: inline-block;
    padding: 5px;
    margin-top: 20px;
    width: 100%; /* Adicionado para garantir que todos os campos de preenchimento tenham o mesmo tamanho */
}

.container-login .login-form .login-form-group .form-error {
    display: block;
    color: #ff0033;
    font-size: 0.9em;
    font-weight: 700;
}

.container-login .login-form .button-form {
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 10px 125px;
    background-color: rgb(147, 184, 203); /* Alterado para verde */
    font-size: 18px;
}

.container-login a:link, a:visited {
    text-decoration: none;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 18px;
    margin: 0 auto;
    color: hsl(130, 89%, 15%);
    text-transform: uppercase;
    font-weight: 800;
    width: 100%;
    padding-top: 15px;
    height: 50px;
    
}


@media only screen and (max-width: 600px){
    .container-login {
        padding: 20px;
        
    }
   
}

@media only screen and (min-width: 601px) and (max-width: 1024px){
    .container-login {
        padding: 40px;
    }
}
